@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700,800&display=swap');

html,
body {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

body {
  width: 100vw;
  height: 100vh;
  font-family: 'Poppins', sans-serif;
  color: lighten(black, 20%);

  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  box-sizing: border-box;
  font-family: inherit;
  outline: none;
  border: none;
  font-weight: 600;
}

section {
  width: 680px;

  h1 {
    margin-bottom: 20px;
    text-align: center;
  }

  .carousel-container {
    .carousel__slider--horizontal {
      height: 400px;
      margin-bottom: 15px;
    }

    .carousel__next-button {
      padding: 10px 0;
      width: 100px;

      transition: all 0.15s ease;

      &:not([disabled]):hover {
        background-color: darken(white, 20%);
      }
    }

    .carousel__back-button {
      padding: 10px 0;
      width: 100px;
      margin-right: 10px;

      transition: all 0.15s ease;

      &:not([disabled]):hover {
        background-color: darken(white, 20%);
      }
    }
  }
}
